import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import HelpIcon from '@material-ui/icons/Help';
import './Card.scss';
import DialogHelp from './Dialog_help';
import Settings from './Settings';

// Dialog
import Container from '@material-ui/core/Container'

const useStyles = makeStyles((theme) => ({
    root: {
        // maxWidth: 345,
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    flex: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
    }
}));


function CardComponent(props) {
    const {
        question,
        title,
        help,

        topics,
        normalizedTopics,
        normalizedTopicsMap,
        topicFilters,
        setTopicFilters,
        setCurrentTopic,

        difficulties,
        normalizedDifficulties,
        normalizedDifficultiesMap,
        difficultyFilters,
        setDifficultyFilters,

        currentQuestions,
        currentQuestionsMap,
        currentQuestion,
        setCurrentQuestion,
    } = props;

    const classes = useStyles();

    return (
        <div className="card-component">
            <Card className={classes.root}>
                <CardHeader
                        classes={{title: classes.flex}}
                        title={
                            <>
                                <Settings
                                    topics={topics}
                                    normalizedTopics={normalizedTopics}
                                    normalizedTopicsMap={normalizedTopicsMap}
                                    topicFilters={topicFilters}
                                    setTopicFilters={setTopicFilters}
                                    setCurrentTopic={setCurrentTopic}
                
                                    difficulties={difficulties}
                                    normalizedDifficulties={normalizedDifficulties}
                                    normalizedDifficultiesMap={normalizedDifficultiesMap}
                                    difficultyFilters={difficultyFilters}
                                    setDifficultyFilters={setDifficultyFilters}

                                    currentQuestions={currentQuestions}
                                    currentQuestionsMap={currentQuestionsMap}
                                    currentQuestion={currentQuestion}
                                    setCurrentQuestion={setCurrentQuestion}
                                />
                                <Typography variant="h5" >
                                    {title}
                                </Typography>
                                <DialogHelp Icon={HelpIcon}>
                                    <Container maxWidth="sm">
                                        {help}
                                    </Container>
                                </DialogHelp>
                            </>
                        }
                />
                <CardContent>
                    <Typography variant="body2" color="textSecondary" component="p">
                        {question}
                    </Typography>
                </CardContent>
                
            </Card>
           
        </div>
    );
}

export default CardComponent;